*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

h1,h2,h3{
    color: rgb(54, 54, 54);
}

body{
    font-family: 'Lato', sans-serif;
    background-color: rgb(233, 232, 237);
}

h3,h4{
    font-weight: 400;
    color: rgb(102, 102, 102);
}

.App {
    height: 100vh;
    transition: all 0.5s ease;
}

.library-active {
    margin-left: 30%;
}

@import "./song";
@import "./player";
@import "./library";
@import "./nav";